.leftbar{
  flex: 2;
  position: -webkit-sticky;
  position: sticky;
  top:70px;
  height: calc(100vh - 70px);
  overflow: scroll;
  background-color:ghostwhite;
  &::-webkit-scrollbar{
    display: none;
  }
  .container{

    padding: 20px;
    hr{
      margin: 20px 0;
      border: none;
      background-color: lightgrey;
      height: 0.5px;
    }
    .menu{
      display: flex;
      flex-direction: column;
      gap: 20px;
      span{
        font-size: 12px;
      }
      
      .user{
        display: flex;
        align-items: center;
        gap: 10px;
        img{
          width: 30px;
          height: 30px;
          object-fit: cover;
          border-radius: 50%;


        }
        span{
          font-size: 14px;

        }
      }
      .item{
        display: flex;
        align-items: center;
        gap: 10px;

        img{
          width: 30px;

        }
        span{

        }

      }
    }
  }
}